<template>
  <div
    class="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8"
  >
    <div class="sm:mx-auto sm:w-full sm:max-w-sm">
      <img
        class="mx-auto h-10 w-auto"
        src="https://tailwindui.com/img/logos/mark.svg?color=indigo&shade=600"
        alt="Your Company"
      />
      <h2
        class="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900"
      >
        Quên mật khẩu
      </h2>
    </div>

    <div class="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
      <form class="space-y-6" action="#" method="POST">
        <div>
          <label
            for="email"
            class="block text-sm font-medium leading-6 text-gray-900"
            >Nhập Email</label
          >
          <div class="mt-2">
            <input
              v-model="formForgot.email"
              id="email"
              name="email"
              type="email"
              autocomplete="email"
              required
              class="px-4 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
          </div>
        </div>

        <div>
          <button
            @click.prevent="submitForm"
            type="submit"
            class="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
          >
            Gửi
          </button>
        </div>
      </form>

      <p class="mt-10 text-center text-sm text-gray-500">
        Nhớ lại rồi?
        {{ " " }}

        <router-link
          to="login"
          class="font-semibold leading-6 text-indigo-600 hover:text-indigo-500"
          >Đăng nhập</router-link
        >
      </p>
    </div>
  </div>
</template>

<script setup>
import axios from "axios";
import { reactive } from "vue";
// import axios from "axios";
import { toast } from "vue3-toastify";
// import { useRouter } from "vue-router";
// import { authStore } from "@/store/authstore";
// const load = ref(false);
// const store = authStore();
// const router = useRouter();
const notifySuccess = (mes) => {
  toast(mes, {
    type: "loading",
    autoClose: 2000,
    position: toast.POSITION.TOP_RIGHT,
  });
};
const formForgot = reactive({
  email: "",
});
const submitForm = async () => {
  const res = await axios.post(
    "http://localhost:8080/forgot-password",
    formForgot
  );
  console.log(res.data);
  if (res.data.payload.message.trim() === "check your email") {
    formForgot.email = "";
    notifySuccess(res.data.payload.message);
  } else {
    notifySuccess(res.data.payload.message);
  }
};
</script>

